* {
  outline: none;
}

body {
  margin: 0;
  overflow-y: overlay;
  font-family: "Poppins Regular";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  width: 6px !important;
  height: 6px !important;
}

body ::-webkit-scrollbar-thumb {
  background-color: hsla(0, 0%, 100%, 0.16);
}

::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
}

::-webkit-scrollbar-track {
  background: hsla(0, 0%, 100%, 0.1);
}

.btn {
  border-radius: 50px;
  padding: 14px 0;
  width: 160px;
  cursor: pointer;
  transition: 0.2s;
  font-family: "Poppins SemiBold";
}

.btn:active {
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}

.primary-btn {
  color: var(--white);
  border: 2px solid linen;
  font-size: 12px;
  background-color: #1f2235;
}
/* @media only screen and (max-width: 414px) {
  .primary-btn {
    font-size: 13px;
  }
} */

.primary-btn:hover {
  color: aliceblue;
  border: 2px solid #ff5823;
  background-color: #1f2235;
}

.highlighted-btn {
  color: var(--white);

  font-size: 12px;
  background-color: #ff5823;
}
@media only screen and (max-width: 568px) {
  .highlighted-btn {
    z-index: -900;
  }
}

.red-border {
  border: 1px solid var(--error-message) !important;
}

.fade-in {
  opacity: 0;
  -webkit-transform: translateY(80px);
          transform: translateY(80px);
  transition: all 0.8s ease;
}

:root {
  --light-blue: #04c3ff;
  --light-blue-shadow: rgb(69 211 255 / 27%);
  --sea-green: #0abead;
  --sea-green-dark: #0cbfae;
  --sea-green-extra-dark: #09bead;
  --sea-green-shadow: rgba(11, 190, 173, 0.4);
  --header-hamburger-background: rgba(11, 190, 173, 0.8);
  --orange: #f57f17;
  --dark-orange: #ff5823;
  --orange-shadow: rgba(245, 127, 23, 0.2);
  --white: #ffffff;
  --black: #333333;
  --disabled-btn: #09bead7a;
  --error-message: #e44f4fd1;
  --toast-error: #e44f4ffa;
  --toast-shadow: #9c9c9ce3;
  --toast-success: #0cbfae;
  --loader-color: #0cbfae;
}
.profile-container{
    display:flex;
    align-items: center;
    justify-content:center;
    height:10%;
    min-height:-webkit-fit-content;
    min-height:-moz-fit-content;
    min-height:fit-content;
    text-align:center;
}

.profile-parent{
    display:flex;
    align-items:center;
    color:aliceblue;
}

.profile-details-name{
    font-size:24px;
    font-family: "Poppins SemiBold", sans-serif;
}

.profile-details-role{
    display:flex;
    flex-direction: column;
    /* top right bottom left */
    margin: 14px 0 24px 0;
}

.profile-details-role h1{
    font-size:40px;
    font-family: cursive;
}

.primary-text{
    color: (--white);
}

.highlighted-text{
    color: var(--dark-orange);
}

.profile-role-tagline{
    font-size: 19px;
    margin: 5px 0 0 0;
    font-family: "Poppins Light";
}

.profile-options .highlighted-btn:hover{
    margin: 0 0 0 28px;
    background-color: cornsilk;
    color: #111;
}

.profile-picture{
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 1px 0px 0px var(--white);
    border-radius: 50%;
    height: 380px;
    width: 380px;
    margin: 0 0 0 100px;
}

.profile-picture-background{
    height: 92%;
    width: 92%;
    background-image: url(/VS_WebPortfolio/static/media/profilephoto.3d74a2b9.jpg);
    background-size: cover;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;

}

.profile-picture-background:hover{ 
    -webkit-transform: scale(1.07); 
            transform: scale(1.07);
    transition: 0.75s ease-out;
}

.colz .colz-icon i{
    padding: 8px;
    color:white;
    padding-left: 0px;
}

.colz .colz-icon i:hover{ 
    -webkit-transform: scale(1.5); 
            transform: scale(1.5);
}

@media screen and (max-width: 1110px){
    .profile-picture{
        margin: 0 0px 0 80px;
    }
    .profile-details-name{
        font-size: 20px;
    }
    .profile-details-role .primary-text{
        font-size: 12px;
    }
    .profile-options .btn{
        width:120px;
        padding : 11px 0;
        z-index: 700;
    }
}

@media screen and (max-width: 910px){
    .profile-picture{
        height: 320px;
        width: 320px;
        margin: 0 0 0 4px;
    }
}

@media screen and (max-width: 810px){
    .profile-picture{
        margin: 0 !important;
    }
    .profile-parent{  
        flex-direction: column-reverse;
        margin: 40px 0 0 0;
    }
    .profile-options{  
        display: flex;
        justify-content: space-between;
    }
    .profile-details{ 
        margin:25px 0 0;
    }
    .profile-options .highlighted-btn{
        margin:0 !important;
    }
    .profile-options, .profile-details-role{
        text-align: center;
    }
}

@media screen and (max-width:375px){
    .profile-picture{
        height: 275px;
        width: 275px;
        margin-top: -90px;
    }
    .profile-details{ 
        width: 90%;
    }
}
.footer-container {
  height: 17.8%;
  width: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
}
.foorer-parent {
  width: 100%;
  height: 100%;
  max-width: 1920px;
}
.footer-parent img {
  max-width: 100%;
  height: 130px;
}
/*IPAD PRO RESPONSIVENESS*/
