  }
  .about-me-parent {
    max-width: 1000px;
    width: 70%;
  }
  .about-me-card {
    display: flex;
    width: 100%;
    box-shadow: 0 0px 20px -2px #1f2235;
  }
  .about-me-profile {
    width: 50%;
    background-image: url(/VS_WebPortfolio/static/media/ProfilePic.ac25f302.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }
  .about-me-details {
    width: 48%;
    text-align: justify;
    padding: 30px;
  }
  .about-me-description {
    font-size: 12px;
  }
  .about-me-highlights {
    margin: 80px 0;
  }
  .highlght-heading {
    font-family: "Poppins Bold";
    font-size: 14px;
    margin: 0 0 5px 0;
  }
  .highlight {
    display: flex;
    align-items: center;
    margin: 5px 0;
