  justify-content: center;
}

form button:hover {
  background-color: #1f2235;
  transition: all ease 1s;
  border: 2px solid #1f2235;
}

button .load {
  width: 100%;
}

button .load img {
  width: 60px;
  color: green;
}
.col i {
  padding: 7px;
  color: #fff;
}
.col i:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

